
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.hello ul {
    padding: 0;
}
.hello ul li {
      list-style: none;
      line-height: 1.5em;
      font-size: 30px;
}















































































































































































































































































































































/* 動畫 */
.lineChart .growLine-enter-active {
    -webkit-transition: all 2s;
    transition: all 2s;
    stroke-dashoffset: 0;
}
.lineChart .growLine-enter {
    stroke-dashoffset: 3000;
}
.lineChart .growDot-enter-active {
    -webkit-transition: all 1s;
    transition: all 1s;
}
.lineChart .growDot-enter {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
}
  /* 說明 */
.lineChart .detail {
    color: gray;
}
  /* 統計圖 */
.lineChart .chartContain {
    max-width: 600px;
    margin: 0 auto;
}
.lineChart .chartContain .chart {
      width: 100%;
      padding-bottom: 100%;
      height: 1px;
      overflow: visible;
}
.lineChart .chartContain .chart .chartWrap .axis.axisY .tick line {
              stroke: #efefef;
              -webkit-transform: translateX(1px);
                      transform: translateX(1px);
}
.lineChart .chartContain .chart .chartWrap .axis.axisY .tick:nth-child(2) line {
                stroke: black;
}
.lineChart .chartContain .chart .chartWrap .line {
          stroke-dasharray: 3000;
}
.lineChart .chartContain .tooltip {
      min-width: 100px;
      background-color: rgba(0, 0, 0, 0.9);
      color: white;
      padding: 10px;
      border-radius: 6px;
      position: absolute;
      text-align: left;
      line-height: 1.5em;
      z-index: 1
}
.lineChart .chartContain .tooltip.hidden {
    visibility: hidden;
}


































































































































































































































/* 動畫 */
.barpChart .growBarp-enter-active {
    -webkit-transition: all 1s;
    transition: all 1s;
}
.barpChart .growBarp-enter {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    opacity: 0;
}
  /* 說明 */
.barpChart .detail {
    color: gray;
}
  /* 統計圖 */
.barpChart .chartContain {
    max-width: 600px;
