      width: 100%;
      padding-bottom: 100%;
      height: 1px;
      overflow: visible;
}
.barpChart .chartContain .chart .chartWrap .axis.axisX .domain {
            display: none;
}
.barpChart .chartContain .chart .chartWrap .axis.axisX .tick line {
              stroke: #efefef;
              stroke-width: 2;
              stroke-dasharray: 1, 6;
              stroke-linecap: round;
