      position: absolute;
      text-align: left;
      line-height: 1.5em;
      z-index: 1
}
.barpChart .chartContain .tooltip.hidden {
    visibility: hidden;
}





















































































































































































































































































/* 說明 */
.donutChart .detail {
    color: gray;
}
  /* 統計圖 */
.donutChart .chartContain {
    max-width: 600px;
    margin: 15px auto;
    overflow: hidden;
}
.donutChart .chartContain .chartWrap {
      width: 100%;
}
@media screen and (min-width: 600px) {
.donutChart .chartContain .chartWrap {
        width: calc(100% - 200px);
        float: left;
}
}
.donutChart .chartContain .chartWrap .chart {
        width: 100%;
        padding-bottom: 100%;
        height: 1px;
        overflow: visible;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
}
.donutChart .chartContain .chartWrap .chart .circle {
          -webkit-transition: 1s;
          transition: 1s;
}
.donutChart .chartContain ul.labelWrap {
      width: 100%;
      padding: 0;
      overflow: hidden;
}
@media screen and (min-width: 600px) {
.donutChart .chartContain ul.labelWrap {
        width: 150px;
        padding-left: 50px;
        float: right;
}
}
.donutChart .chartContain ul.labelWrap li {
        list-style: none;
        display: block;
        overflow: hidden;
        margin: 15px 10px 0 0;
        float: left;
}
.donutChart .chartContain ul.labelWrap li .color {
          display: inline-block;
          width: 10px;
          height: 10px;
          margin: 5px 5px 0 0;
          float: left;
}
.donutChart .chartContain ul.labelWrap li .region {
          float: left;
          margin-right: 10px;
}
.donutChart .chartContain ul.labelWrap li .percentage {
          float: left;
          color: rgba(0, 0, 0, 0.4);
}
.donutChart .chartContain .tooltip {
      min-width: 100px;
      background-color: rgba(0, 0, 0, 0.9);
      color: white;
      padding: 10px;
      border-radius: 6px;
      position: absolute;
      text-align: left;
      line-height: 1.5em;
      z-index: 1;
}
.donutChart .chartContain .tooltip.hidden {
    visibility: hidden;
}
